colors: 
- Brown: #561E18
- Red: #A82025
- Tan: #BDA05E
- Gold: #B18A30
- Black: #15100C
*/
body {
  background-color: #f8f3f0;
}

.react-tabs__tab-panel:not(.react-tabs__tab-panel--selected) {
  display: none;
}

button {
  padding: 10px;
  background: #561E18;
  border: 1px solid #561E18;
  border-radius: 5px;
  color: #fff;
  margin: 2px;
}

.add-button {
  margin: 10px;
}

input, select {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #15100C;
  background: #fff;
}

.url {
  width: 75%;
}

.tab-list {
  display: inline-flex;
  padding: 10px;
  list-style-type: none;
}

.tab:active {
  border: unset;
}

.tab {
  margin: 0 5px 5px auto;
  padding: 1px;
  border-bottom: 2px solid #15100C;
}

.selected-tab {
  border-bottom: 4px solid #B18A30;
}

.cm-scroller {
  border: 2px solid #efefef;
}

.response-status {
  margin-top: 5px;
}`, "",{"version":3,"sources":["webpack://./src/renderer/src/App.css"],"names":[],"mappings":"AAAA;;;;;;;CAOC;AACD;EACE,yBAAyB;AAC3B;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,yBAAyB;EACzB,kBAAkB;EAClB,WAAW;EACX,WAAW;AACb;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,aAAa;EACb,kBAAkB;EAClB,yBAAyB;EACzB,gBAAgB;AAClB;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,oBAAoB;EACpB,aAAa;EACb,qBAAqB;AACvB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,sBAAsB;EACtB,YAAY;EACZ,gCAAgC;AAClC;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,eAAe;AACjB","sourcesContent":["/* \ncolors: \n- Brown: #561E18\n- Red: #A82025\n- Tan: #BDA05E\n- Gold: #B18A30\n- Black: #15100C\n*/\nbody {\n  background-color: #f8f3f0;\n}\n\n.react-tabs__tab-panel:not(.react-tabs__tab-panel--selected) {\n  display: none;\n}\n\nbutton {\n  padding: 10px;\n  background: #561E18;\n  border: 1px solid #561E18;\n  border-radius: 5px;\n  color: #fff;\n  margin: 2px;\n}\n\n.add-button {\n  margin: 10px;\n}\n\ninput, select {\n  padding: 10px;\n  border-radius: 5px;\n  border: 1px solid #15100C;\n  background: #fff;\n}\n\n.url {\n  width: 75%;\n}\n\n.tab-list {\n  display: inline-flex;\n  padding: 10px;\n  list-style-type: none;\n}\n\n.tab:active {\n  border: unset;\n}\n\n.tab {\n  margin: 0 5px 5px auto;\n  padding: 1px;\n  border-bottom: 2px solid #15100C;\n}\n\n.selected-tab {\n  border-bottom: 4px solid #B18A30;\n}\n\n.cm-scroller {\n  border: 2px solid #efefef;\n}\n\n.response-status {\n  margin-top: 5px;\n}"],"sourceRoot":""}]);
